.chatScreen_message {
    display: flex;
    align-items: center;
    padding: 20px;
  }
  
  .chatScreen_text {
    margin-left: 10px;
    background-color: lightgray;
    padding: 15px;
    border-radius: 20px;
  }
  
  .chatScreen_timestamp {
    text-align: center;
    padding: 20px;
    color: gray;
  }
  
  .chatScreen_textUser {
    margin-left: auto;
    background-color: #28b3cd;
    padding: 15px;
    border-radius: 20px;
    color: white;
  }
  
  .chatScreen_input {
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid lightgray;
  }
  
  .chatScreen_inputField {
    flex: 1;
    padding: 10px;
    border: none;
  }
  
  .chatScreen_inputButton {
    border: none;
    margin-right: 20px;
    background-color: white;
    font-weight: bolder;
    color: #fe3d71;
  }